.home-page-wrapper {
  position: relative;
  width: inherit;
}

.home-page-illustration {
  position: relative;
  overflow: hidden;
  display: flex;
  width: 100%;
  height: 269px;
  border-radius: 40px;
}

.illustration-img {
  width: 100%;
  height: 100%;
  background-image: url("/src/assets/image/illustration.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.illustration-img::before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}

.home-page-illustration:hover .illustration-img,
.home-page-illustration:focus .illustration-img {
  transform: scale(1.2);
  transition: all 1s;
}

.home-page-action {
  position: relative;
  margin-top: 50px;
}

.action-popular-btn {
  display: flex;
  align-items: center;
  margin: auto;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 18px;
  width: 292px;
  height: 42px;
  border: 3px solid #3186f5;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
}

.popular-btn-option {
  cursor: pointer;
  width: 146px;
  display: flex;
  justify-content: space-around;
}

.action-search-btn {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}

.popular-active {
  width: 146px;
  height: 42px;
  border-radius: 25px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #f5f5f5;
  background-color: #3186f5;
  transition: all 0.4s ease-in-out;
}

.home-page-content {
  margin-top: 28px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.content-box-next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 135px;
  background-color: #3186f5;
  border-radius: 20px;
}

.illustration-text {
  position: absolute;
  color: #f5f5f5;
  margin-top: 61px;
  margin-left: 26px;
  font-size: 45px;
  font-weight: 600;
}

.illustration-text-bold {
  color: #3186f5;
}

.arrow-next {
  font-size: 35px;
  color: #f5f5f5;
}

.circled-icon {
  font-size: 20px;
  color: #3186f5;
}

.button-mulai-mencari {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  gap: 12px;
  top: -6px;
  width: 245px;
  height: 48px;
  background: #3186f5;
  border-radius: 40px;
  margin: auto;
}

.btn-mencari {
  text-decoration: none;
}

.mencari-narasumber {
  font-family: "Poppins";
  font-weight: 600;
  font-size: 12.0634px;
  color: #f5f5f5;
}

.icon-mencari {
  width: 28px;
}
